import { SpaRounded } from "@material-ui/icons";
import { useState } from "react";
import "./contact.scss"
import {GitHub, Email, LinkedIn, ListAlt} from "@material-ui/icons"
import { ImFileText2 } from 'react-icons/im';

export default function Contact() {
    const [message,setMessage] = useState(false)
    
    const handleSubmit = (e)=>{        
        e.preventDefault();
        setMessage(true)
    }
    return (
        <div className="contact" id="contact">
                <h1>Contact</h1>
                <form onSubmit={handleSubmit}>
                    <input type="text" placeholder=" Email"/>
                    <textarea placeholder=" Message"></textarea>
                    <button type="submit">Send</button>
                    {message && <span>Thanks, I'll reply as soon as I can.</span>}
                </form>
                <div className="footer">
                <div className="itemContainer">

                    <a href="https://www.linkedin.com/in/roblieblang" target="blank"><LinkedIn className="icon" /></a>
                    <a href="https://www.linkedin.com/in/roblieblang" target="blank"><span>LinkedIn</span></a>
                    </div>
                    <div className="itemContainer">
                      <a href="https://github.com/roblieblang" target="blank"><GitHub className="icon" /></a>
                      <a href="https://github.com/roblieblang" target="blank"><span>GitHub</span></a>
                    </div>
                    <div className="itemContainer">
                        <a href="https://drive.google.com/file/d/1szM1C2VXm85Z2GS7kXJjplrF9deB3E1q/view?usp=sharing" target="blank">
                            <ImFileText2 className="icon" />
                        </a>
                        <a href="https://drive.google.com/file/d/1szM1C2VXm85Z2GS7kXJjplrF9deB3E1q/view?usp=sharing" target="blank"><span>Resume</span></a> 
                    </div>
                    <div className="itemContainer">
                        <a href="mailto: rlieblan@depaul.edu" target="blank"><Email className="icon" /></a>
                        <a href="mailto: rlieblan@depaul.edu" target="blank"><span>Email</span></a>
                    </div>

                </div>
                
            </div>
    )
}
