import { useEfect, useState } from "react";
import ProjectsList from "../projectsList/ProjectsList";
import "./projects.scss"

export default function Projects() {
        const [selected, setSelected] = useState("featured");
    const list = [
        {
            id: "featured", 
            title: "Featured"
        }, 
        {
            id: "website",
            title: "Personal Website"
        },
        {
            id: "tetris",
            title: "Tetris.JS"
        },
        {
            id: "django",
            title: "Django Project"
        },
        {
            id: "pygame",
            title: "Pygame Project"
        }
    ];

    return (
        <div className="projects" id="projects">  
            <h1>Projects</h1>
            <ul>
                {list.map ((item) => (
                    <projectsList title={item.title} 
                    active={selected === item.id} 
                    setSelected={setSelected}
                    id={item.id}
                    />
                ))}
            </ul>
            <div className="container">
                <a href="https://github.com/roblieblang/roblieblang.github.io" target="blank">
                    <div className="item">
                        <img src="/assets/portfolioScreen.png" alt="portfolio image" />
                    <h3>Portfolio Website</h3>
                    </div>
                </a>
                <a href="https://github.com/roblieblang/severe-weather-map" target="blank">
                    <div className="item">
                        <img src="/assets/mapScreen.jpg" alt="map image" />
                    <h3>Severe Weather Map</h3>
                    </div>
                </a>
                <a href="https://roblieblang.github.io/tetris" target="blank">
                    <div className="item">
                        <img src="/assets/tetrisScreen.png" alt="Tetris image"/>
                    <h3>Tetris</h3>
                    </div>
                </a>
                <a href="https://github.com/roblieblang/space_skirmish" target="blank">
                    <div className="item">
                        <img src="/assets/spaceScreen.jpg" alt="Space Skirmish image"/>
                    <h3>Space Skirmish</h3>
                    </div>
                </a>
            </div>
        </div>
    )
}
